.image {
  transition: opacity 1000ms cubic-bezier(0.645, 0.045, 0.355, 1), filter 1000ms cubic-bezier(0.645, 0.045, 0.355, 1);
  opacity: 0.6;
}
.image img {
  border: 3px solid rgba(255, 255, 255, 0.5);
  width: 100%;
  height: auto;
  transition: all 1000ms cubic-bezier(0.645, 0.045, 0.355, 1);
}
.image:hover {
  opacity: 1;
}
.image:hover img {
  box-shadow: 0px 0px 50px 1px rgba(255, 255, 255, 0.3);
}
.image.hidden {
  opacity: 0;
  filter: blur(20px);
}

#imageAladdin {
  align-self: center;
}

#imageEugene, #imageJeanLeon {
  width: 75%;
  margin: 3dvh auto;
}

#history #history2paragraph {
  display: flex;
  align-items: end;
}

#culture .quote h2 {
  width: 75%;
}
#culture .quote .emphasis {
  background-color: rgba(255, 255, 255, 0);
}

#beurette .quote {
  display: flex;
  justify-content: center;
}
#beurette .quote h2 {
  width: 100%;
}
#beurette .paragraph-30-70:has(.quote) {
  gap: 3dvw;
}

#analysis p, #analysis h1, #analysis a {
  color: rgba(51, 48, 48, 0.85);
}
#analysis .read-more::after {
  background: rgba(51, 48, 48, 0.85);
}
#analysis .column-count-2 p {
  column-count: 2;
  column-gap: 3dvw;
}
#analysis #analysisTitle {
  margin-top: 5dvh;
}

#men #men1quote {
  width: 75%;
}

#bibliography ul {
  direction: rtl;
}
#bibliography li::marker {
  font-size: 6pt;
}
#bibliography li {
  font-family: "Didot", serif;
  color: white;
  font-size: min(max(1vw, 12pt), 14pt);
  z-index: 1;
  line-height: min(max(2vw, 24pt), 28pt);
  transition: all 1000ms cubic-bezier(0.645, 0.045, 0.355, 1);
  padding-right: 10px;
}
#bibliography li.hidden {
  opacity: 0;
  filter: blur(20px);
  transform: translateY(50px);
}
#bibliography li .italic {
  font-style: italic;
}

@media screen and (max-width: 740px) {
  .image {
    opacity: 1;
  }
  .image img {
    width: 100%;
    height: auto;
  }
  #imageEugene, #imageJeanLeon {
    width: 100%;
  }
  #culture .quote h2 {
    width: 100%;
  }
  #imageAladdin {
    margin: 3dvh 0;
  }
  #analysis .column-count-2 p {
    column-count: 1;
    column-gap: 0;
  }
  #men #men1quote {
    width: 100%;
  }
}